import React ,{Component} from 'react';
import './login.scss';
import { Formik } from 'formik';
import * as Yup from 'yup';
import {Error} from './error';
import auth from '../../components/auth/auth';


const validationSchema = Yup.object().shape({
    email: Yup.string()
        .email('Must be a valid email address')
        .max(100,'Must be shorter than 100')
        .required(),
    pass: Yup.string()
        .min(1,'Must be  longer than 1')
        .max(15,'Must be shorter than 15')
        .required()
});

export  class  Login extends Component{
    render (){
        return (
            <div className='container'>
                    <div className="login">
                    <div className="login-screen">
                        <div className="app-title mb-3">
                            <h1>Login</h1>
                        </div>
                        <Formik  
                        initialValues={{email:"",pass:""}}
                        validationSchema={validationSchema}
                        onSubmit={(values , {setSubmitting,resetForm}) => {
                            setSubmitting(true);
                            setTimeout(() => {
                                alert(JSON.stringify(values))
                                resetForm();
                                setSubmitting(false);
                                localStorage.setItem('userData',JSON.stringify(values))
                                // this.props.history.push('/home');
                            },500)
                        } }>
                            {({values , touched , errors , handleChange , handleBlur,handleSubmit,isSubmitting}) => (
                                <form onSubmit={handleSubmit}>
                                <div className="form-group">
                                    <input type="email" name='email'
                                    className={touched.email && errors.email ? 'form-control border border-danger' : 'form-control '}  value={values.email} placeholder="Email"
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                     />
                                    <Error  touched={touched.email} message={errors.email}/>
                                </div>
                                <div className="control-group">
                                    <input type="password" name='pass' value={values.pass}className={touched.pass && errors.pass ? 'form-control border border-danger' : 'form-control '} placeholder="Password"
                                     onChange={handleChange}
                                     onBlur={handleBlur}/>
                                    <Error  touched={touched.pass} message={errors.pass}/>
                                </div>
                                <button className="btn btn-primary btn-block mt-3" disabled={!values.email || !values.pass} type='submit' onClick={() => {
                                    auth.logIn(() => {
                                        this.props.history.push('/products')
                                    })
                                }} >login</button>
                            </form>
                            )}
                          </Formik>
                    </div>
                    </div>
            </div>
        )
    }
    
}