import React from 'react';
import { connect } from 'react-redux';
import './cartItem.scss';
import {removeFromCart} from '../../store/Action/actions';


function CartItem(props){
    const {item,index} = props;
    const {product} = item;

    return (
          <div className="parentCart mt-3 mb-3" >
                <img src={product.image} alt='' className="card-img-top" width={'100%'} height={'150px'} />
                <div className="details p-3">
                    <h5>{product.name}</h5>
                    <p >Price : {product.price}$</p>
                    <p>quantity : {item.quantity}</p>
                    <p>total : {product.price * item.quantity}$</p>
                    <button onClick={() => props.removeFromCart(index)} className="btn btn-sm">
                    <i className='fa fa-trash'></i></button>
                </div>
            </div>                
    )
}


const mapDispatchToProps = (dispatch) => {
   return{
    removeFromCart : (index) => dispatch(removeFromCart(index))
   }
}




export default connect(null,mapDispatchToProps)(CartItem);