import {ADD_TO_CART} from '../Action/type';
import {REMOVE_FROM_CART} from '../Action/type';
import {CLEAR_CART} from '../Action/type';


export default function cartReduce(state , action){
console.log(state,action)
    switch(action.type){
        case ADD_TO_CART:{
            return {
                cart : [
                    ...state.cart, 
                    {
                        product : action.productInfo,
                        quantity: action.quantity
                    }
                ]
            }
        }
        case REMOVE_FROM_CART:{
            const itemIndex = action.index;
            const newState = {...state};
            newState.cart.splice(itemIndex,1);
            return newState;
        }
        case CLEAR_CART:{
            const newState = {...state};
            newState.cart = [];
            return newState;
        }
        
        default:
            return state;
    }


}