import React ,{Component} from 'react';
import CartItem from '../../components/cartItem/cartItem';
import { connect } from 'react-redux';
import {clearCart} from '../../store/Action/actions'
class Cart extends Component{
    placeOrder = () => {
        this.props.clearCart();
        alert('we recieved your order and we are working on it')
    }
    render(){
        return (
            <div className='container mt-3'>
                 <div className='row'>
                    {this.props.cartItems.map((item,index) => 
                        <div className='col-3' key={index}>
                        <CartItem item={item} index={index} />
                        </div>
                    )}
                 </div>
                <p> total : {this.props.total}$</p>
                <button style={this.props.cartItems.length !== 0 ? {} : { display: 'none' }}  className='btn btn-primary btn-block mb-5' onClick={this.placeOrder}>Place Order</button>
            </div>
         )
    }
    
}

const mapStateToProps = (state) => {
    return {
        cartItems : state.cart,
        total : state.cart.reduce((total,item) => total + item.quantity * item.product.price , 0)
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        clearCart : () => dispatch(clearCart())
    }
}


export default connect(mapStateToProps,mapDispatchToProps)(Cart);