import React ,{Component} from 'react';
import getProduct from '../../api/products';
import { connect } from 'react-redux';
import {addToCart} from '../../store/Action/actions';
import './product.scss';

class Product extends Component{
    state = {
        loading:true,
        quantity:1,
        product : {}
    }

    componentDidMount(){
        const id = this.props.match.params.id
        getProduct.getById(parseInt(id)).then(product => {
            this.setState({
                product,
                loading:false
            })
        })
    }

    handleQuantity = (event) => {
       const value = event.target.value;
        if(value < 1)
            return;
        this.setState({
            quantity : value
        })
    }


    addToCart = (product) => {
        this.props.addToCart(product,this.state.quantity);
        this.setState({
            quantity : 1
        })
    }

   render(){
       if(this.state.loading)
        return 'Loading...';
        const product = this.state.product;
        const quantity = this.state.quantity;
        return (    
            <div className='mt-3 container '>
                <div className='row'>
                    <div className='col-6'>
                        <div className='product'>
                            <img src={product.image} alt='' width={'100%'} height={'350px'} />
                        </div>
                    </div>
                    <div className='col-6'>
                        <div className='product'>
                            <h5>{product.name}</h5>
                            <p>Price : {product.price}$</p>
                            <input  className='form-control' type='number' value={quantity} onChange={this.handleQuantity} />
                            <p className='mt-2'>Total Price : {quantity * product.price}$</p>
                            <button className='btn btn-primary' onClick={() => this.addToCart(product)}>Add To Cart</button>
                        </div>
                    </div>
                </div>
            </div>
        )
   }
}


const mapDispatchToProps = (dispatch) => {
        return {
            addToCart : (productInfo,quantity) => dispatch(addToCart(productInfo,quantity))
        }
}


export default connect(null,mapDispatchToProps)(Product);